import React from "react";
import styled from "styled-components";

const StyledNavigationDrawerButton = styled.button`
  width: 48px;
  height: 48px;

  border-radius: 50%;

  background-color: ${(props) => props.theme.color.blue};
`;
export default function NavigationDrawerButton(): JSX.Element {
  return <StyledNavigationDrawerButton></StyledNavigationDrawerButton>;
}
